import React, {Component} from 'react';
import PropTypes from 'prop-types';
import styled from 'styled-components';

const colors = {
    highlight: '#007AFF',
};

const MatrixItem = (props) => {
    const {
        className,
        children,
        value,
        onMouseEnter,
        onMouseLeave,
        binary,
        layout,
        itemLayout,
    } = props;
    return (
        <React.Fragment>
            <td className={className}>
                <div onHover={() => console.log('kjsds')}>
                    <StyledBlankLink
                        href={'#'}
                        onMouseEnter={onMouseEnter}
                        onMouseLeave={onMouseLeave}
                    >
                        {layout.binary ? (
                            <StyledMatrixItemBinary {...props} />
                        ) : (
                            <StyledMatrixItemValue {...props}>
                                {value}
                            </StyledMatrixItemValue>
                        )}
                    </StyledBlankLink>
                </div>
            </td>
        </React.Fragment>
    );
};

const StyledMatrixItemBinary = styled.span`
    background: ${(props) =>
        props.highlight ? colors.highlight : props.background || 'translucent'};

    color: ${(props) => (props.highlight ? 'white' : props.color || 'black')};
    box-sizing: border-box;
    -moz-box-sizing: border-box;
    -webkit-box-sizing: border-box;
    height: 16px;
    width: 16px;
    border-radius: ${(props) =>
        props.layout.binaryShape === 'circle' ? '50%' : '0%'};
    display: inline-block;
    margin-left: 3px;
    margin-top: 2px;
    background-color: ${(props) => (props.value === 1 ? 'black' : 'white')};
    border: 1px solid
        ${(props) => {
            if (props.value === 0) {
                if (props.highlight) {
                    if (props.itemLayout) {
                        return props.itemLayout.background || colors.highlight;
                    }
                    return colors.highlight;
                }

                if (props.itemLayout) {
                    if (props.itemLayout.highlight) {
                        if (props.itemLayout.background) {
                            return props.itemLayout.background;
                        }
                        return colors.highlight;
                    }
                }

                return 'black';
            }

            return 'translucent';
        }};

    background: ${(props) => {
        if (props.value === 1) {
            if (props.highlight) {
                if (props.itemLayout) {
                    return props.itemLayout.background || colors.highlight;
                }
                return colors.highlight;
            }

            if (props.itemLayout) {
                if (props.itemLayout.highlight) {
                    if (props.itemLayout.background) {
                        return props.itemLayout.background;
                    }
                    return colors.highlight;
                }
            }
            return 'black';
        }
        return 'white';
    }};

    text-decoration: none;
`;

const StyledMatrixItemValue = styled.div`
    text-decoration: none;
`;

const StyledBlankLink = styled.a`
    text-decoration: none;
    color: black;
    &:hover ${StyledMatrixItemValue} {
        background: ${colors.highlight};
        color: white;
    }
    &:hover ${StyledMatrixItemBinary} {
        background: ${colors.highlight};
        color: white;
    }
`;

const StyledMatrixItem = styled(MatrixItem)``;

const TopItem = ({className, children, value}) => (
    <React.Fragment>
        <td className={className} style={{textAlign: 'center'}}>
            {value}
        </td>
    </React.Fragment>
);

const StyledTopItem = styled(TopItem)`
    color: ${(props) => props.color || 'black'};
    background: ${(props) => props.background || 'lightGray'};
    border-radius: ${(props) => (props.shape === 'circle' ? '50%' : '50%')};

    height: 16px;
    width: 16px;
    line-height: 16px;
    padding: 4px;

    ${(props) => {
        if (props.highlight) {
            return 'background:' + colors.highlight + '; color:white';
        }
    }}
`;

const LeftItem = (props) => {
    const {i, className, value} = props;
    return <div className={className}> {value} </div>;
};

const StyledLeftItem = styled(LeftItem)`
    color: ${(props) => props.color || 'black'};
    background: ${(props) => props.background || 'lightGray'};
    border-radius: ${(props) => (props.shape === 'circle' ? '50%' : '0')};
    text-align: center;
    padding: 4px;
    height: 16px;
    width: 16px;
    line-height: 16px;
    margin-left: auto;

    ${(props) => {
        if (props.highlight) {
            return 'background:' + colors.highlight + '; color:white';
        }
    }}
`;

const LeftHeaderElement = (props) => {
    return <td></td>;
};

const extractValueFromLeftData = (data, i) => {
    if (data.left.data.length > 0) {
        return data.left.data[i];
    }
};

const Table = styled.table`
    border-spacing: 5px 4px;
    padding-top: ${(props) => {
        if (props.data.right.data) {
            if (props.data.right.data.length > 0) {
                return '40px';
            }
        }
        return '0px';
    }};

    padding-right: ${(props) => {
        if (props.data.right.data) {
            if (props.data.right.data.length > 0) {
                return '60px';
            }
        }

        return '0px';
    }};
`;

const Content = styled.div``;

const Item = ({className, children}) => {
    return (
        <td className={className}>
            <div className="content">{children}</div>
        </td>
    );
};

const StyledItem = styled(Item)`
    .content {
        text-align: center;
        color: ${(props) => props.layout.color || 'black'};
        font-family: 'Open Sans', 'HelveticaNeue', 'Helvetica Neue', Helvetica,
            Arial, sans-serif;
        vertical-align: center;
        line-height: 16px;
    }
`;

const HeaderItemBottom = ({className, children}) => {
    return (
        <th className={className}>
            <div className="content">{children}</div>
        </th>
    );
};

const StyledHeaderItemBottom = styled(HeaderItemBottom)`
    margin-left: auto;
    text-align: right;
    .content {
        color: ${(props) => props.layout.color || 'black'};
    }
`;

const HeaderItemRight = ({className, children}) => {
    return (
        <th className={className}>
            <div className="content">{children}</div>
        </th>
    );
};

const StyledHeaderItemRight = styled(HeaderItemRight)`
    margin-left: auto;
    text-align: right;

    .content {
        color: ${(props) => props.layout.color || 'black'};
        transform: rotate(-45deg);
        position: absolute;
        margin-top: -30px;
        margin-left: -5px;
    }
`;

const DoubleRect = ({className, children}) => {
    return (
        <div className={className}>
            <div className="content">{children}</div>
        </div>
    );
};
const StyledDoubleRect = styled(DoubleRect)`
    width: 50px;
    height: 50px;
    margin-left: 100px;
    position: relative;
    background: ${colors.primBlue};
    overflow: hidden;
    color: white;

    text-align: center;
    vertical-align: middle;
    line-height: 50px;

    :before {
        width: 100%;
        height: 50%;
        position: absolute;
        content: '';
        top: 50%;
        background: ${colors.primGreen};
        left: 0px;
    }

    .content {
        position: absolute;
        width: 100%;
        height: 100%;
    }
`;

export default class MatrixOld extends Component {
    constructor(props, context) {
        super(props, context);
        this.state = {
            enteredItem: {
                i: -1,
                j: -1,
            },
        };
    }

    setEnteredItemState(set, i, j) {
        if (set) {
            this.setState({
                enteredItem: {
                    i: i,
                    j: j,
                },
            });
        } else {
            this.setState({
                enteredItem: {
                    i: -1,
                    j: -1,
                },
            });
        }
    }

    render() {
        const {id, setProps, data} = this.props;

        const sanitizeValue = (value) => {
            if (typeof value === 'number') {
                return {
                    value: value,
                };
            }

            return value;
        };

        return (
            <Content id={id}>
                <Table data={data}>
                    <thead>
                        <tr>
                            <LeftHeaderElement data={data} />
                            {data.top.data.map((value, j) => (
                                <StyledTopItem
                                    value={sanitizeValue(value).value}
                                    j={j}
                                    color={
                                        this.state.enteredItem.j === j &&
                                        'white'
                                    }
                                    background={
                                        this.state.enteredItem.j === j &&
                                        colors.highlight
                                    }
                                    highlight={value.highlight}
                                    itemlayout={value.layout}
                                    layout={data.top.layout}
                                />
                            ))}

                            {data.right.data.map((row, i) => (
                                <StyledHeaderItemRight layout={row.layout}>
                                    {row.layout.title}
                                </StyledHeaderItemRight>
                            ))}
                        </tr>
                    </thead>

                    <tbody>
                        {data.center.data.map((matrixRow, i) => (
                            <tr>
                                <StyledLeftItem
                                    value={
                                        sanitizeValue(
                                            extractValueFromLeftData(data, i)
                                        ).value
                                    }
                                    color={
                                        this.state.enteredItem.i === i &&
                                        'white'
                                    }
                                    background={
                                        this.state.enteredItem.i === i &&
                                        colors.highlight
                                    }
                                    highlight={
                                        sanitizeValue(
                                            extractValueFromLeftData(data, i)
                                        ).highlight
                                    }
                                    itemlayout={
                                        sanitizeValue(
                                            extractValueFromLeftData(data, i)
                                        ).layout
                                    }
                                    layout={data.left.layout}
                                />

                                {matrixRow.map((value, j) => (
                                    <StyledMatrixItem
                                        value={sanitizeValue(value).value}
                                        i={i}
                                        j={j}
                                        onMouseEnter={() =>
                                            this.setEnteredItemState(true, i, j)
                                        }
                                        onMouseLeave={() =>
                                            this.setEnteredItemState(
                                                false,
                                                i,
                                                j
                                            )
                                        }
                                        highlight={
                                            sanitizeValue(value).highlight
                                        }
                                        itemLayout={sanitizeValue(value).layout}
                                        layout={data.center.layout}
                                    />
                                ))}

                                {data.right.data.map((row, j) => (
                                    <StyledItem
                                        itemLayout={
                                            sanitizeValue(row.data[i]).layout
                                        }
                                        layout={row.layout}
                                    >
                                        {sanitizeValue(row.data[i]).value}
                                    </StyledItem>
                                ))}
                            </tr>
                        ))}

                        {data.bottom.data.map((row, i) => (
                            <tr>
                                <StyledHeaderItemBottom layout={row.layout}>
                                    {row.layout.title}
                                </StyledHeaderItemBottom>
                                {row.data.map((value, j) => (
                                    <StyledItem
                                        itemLayout={sanitizeValue(value).layout}
                                        layout={row.layout}
                                    >
                                        {sanitizeValue(value).value}
                                    </StyledItem>
                                ))}
                            </tr>
                        ))}
                    </tbody>
                </Table>
            </Content>
        );
    }
}

MatrixOld.defaultProps = {};

MatrixOld.propTypes = {
    /**
     * The ID used to identify this component in Dash callbacks.
     */
    id: PropTypes.string,

    /**
     * The data that needs to be displayed
     */

    data: PropTypes.object,

    /**
     * Dash-assigned callback that should be called to report property changes
     * to Dash, to make them available for callbacks.
     */
    setProps: PropTypes.func,
};
